@import 'variables';

.navbarwrapper{
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    max-height: 31vh;
    width: 100vw;
    -webkit-touch-callout: none;  
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}



.logo{
    @media screen and (max-width: $medium) {
        height: 6vh;
    }
    cursor: pointer;
    
    min-height: 12vh;
    height: 15vh;
    // max-height: 15vh;
    width: 70%;
    margin-top: 5px;
    min-width: 280px;
    max-width: 800px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
   }

.sociallinkwrapper{
    display: flex;
    align-items: center;
    position: absolute;
    right:1%;
    top:2%;
    gap: 5px;

    a:nth-child(-n+2){
        transform: scale(1.2);
        margin-right: 2px;
    }
}  

.sociallink{
    color: black;
    cursor: pointer;
}

.branchwrapper{
    max-height: 60%;
    min-width: 280px;
    // width: 50vw;
    max-width: 800px;
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
    // display: flex;
    .branch{
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        margin: 3px;
        position: relative;
        

        .branchlogo{
            width: 100%;
            height: 10vh;
            max-height: 123px;
            max-width: 117px;
            @media screen and (max-width: $medium) {
                height: 15vw;
                width: 15vw;
            }
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            cursor: pointer;
        }
        
        .branchname{
            cursor: pointer;
            font-size: 1.2rem;
            font-weight: 700;
            bottom: 1%;
            min-width: 9.5rem;
            background-color: $chocolate;
            color: white;
            padding: 3px;
            margin-top: 1vh;
            border-radius: 5px;
            div{
                text-transform: uppercase;
                font-weight: 300;
            }
        }
    }
}

.branchnamehover{
    -webkit-box-shadow: 1px 1px 15px 7px rgba(0,0,0,0.76); 
    box-shadow: 1px 1px 15px 7px rgba(0,0,0,0.76);
    transition: -webkit-box-shadow 1s, box-shadow 1s;
}

.glowgrey{
    filter: drop-shadow(5px -5px 10px #cfd5d8 ) drop-shadow(-5px -5px 10px #cfd5d8) saturate(130%);
    transition: filter 1s;
}
.glowgreen{
    filter: drop-shadow(5px -5px 10px #bee8cf) drop-shadow(-5px -5px 10px #bee8cf) saturate(130%);
    transition: filter 1s;
}
.glowocher{
    filter: drop-shadow(5px -5px 10px $ocherglow) drop-shadow(-5px -5px 10px $ocherglow) saturate(130%);
    transition: filter 1s;
}
.glowblue{
    filter: drop-shadow(5px -5px 10px #bedae8) drop-shadow(-5px -5px 10px #bedae8) saturate(130%);
    transition: filter 1s;
}

.focusbranch{
    transform: scale(1.2) ;
    transition: transform 0.5s;
}

.faded{
    transform: scale(1.0);
    transition: transform 2s;
}

.navbarline{
    display: flex;
    justify-content: center;
    margin-top: 5px;
    width: 100%;
    background-color: $chocolate;
    .nav-buttons{
        display: flex;
        justify-content: space-around;
        min-width: 660px;
        width: 60%;
        max-width: 800px;
        color: white;
        font-size: 1.2rem;
        div{
            cursor: pointer;

        }
    }
    .quadrat{
        margin-right: auto;
        height: 2rem;
        width: 2rem;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        cursor: pointer;
        filter: invert(100%);
    }
    .phone{
        filter: invert(100%);
    }

    @keyframes wiggle {
        0% { transform: translate(1px, 1px) rotate(0deg); }
        10% { transform: translate(-1px, -2px) rotate(-1deg); }
        20% { transform: translate(-3px, 0px) rotate(1deg); }
        30% { transform: translate(3px, 2px) rotate(0deg); }
        40% { transform: translate(1px, -1px) rotate(1deg); }
        50% { transform: translate(-1px, 2px) rotate(-1deg); }
        60% { transform: translate(-3px, 1px) rotate(0deg); }
        70% { transform: translate(3px, 1px) rotate(-1deg); }
        80% { transform: translate(-1px, -1px) rotate(1deg); }
        90% { transform: translate(1px, 2px) rotate(0deg); }
        100% { transform: translate(1px, -2px) rotate(-1deg); }
    }

    .hoverphone{
        animation: wiggle 1s;
    }
}

.dropdown{
    position: absolute;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    font-size: 1.2rem;
    top: 100%;
    z-index: 1;
    color: black;
    background: rgba(218, 206, 206, 0.644);
    border-radius: 5px;
    box-shadow: 5px 4px 30px rgba(117, 116, 116, 0.326);
    backdrop-filter: blur(5.9px);
    -webkit-backdrop-filter: blur(5.9px);
    border: 1px solid rgba(241, 229, 229, 0.674);
    h3{
        margin: 1vh;
        text-transform: uppercase;
    }
    h4{
        margin: 1vh;
        margin-top: 0;
    }
    :last-child{
        border-bottom: none;
        margin-bottom: 0;
    }
    :first-child{
        margin-top: 2px;
    }
}

.treatmenttitle{
    cursor: pointer;
    list-style-type: none;
    text-align: left;
    padding-bottom: 5px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.635);
    padding: 2px;
    border-radius: 2px;
   
}


