@import 'variables';


html {
    @media screen and (max-width: $medium) {
        font-size: 60%;
    }
}

body {
    margin: 0;
    font-family: 'Roboto';
    max-width: 100vw;
}

a{
    outline: none;
    text-decoration: none;
}

.app{
    width: 100vw;
}

.container{
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;

}